@import url("https://fonts.googleapis.com/css2?family=Nunito&display=swap");

/* global */

body {
  font-family: Nunito;
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
}

.wrapper {
  margin: auto;
  width: 1170px;
}

h1 {
  font-size: 40px;
  font-weight: 400;
  line-height: 40px;
  text-align: center;
}

section {
  margin-bottom: 140px;
}

button {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0em;
  text-align: center;
  width: 100px;
  background: #f4e041;
  border-radius: 80px;
  border: none;
  transition: 300ms;
}

.red {
  color: red;
  margin: 0;
  transition: 300ms;
}

/* global end*/

/* main-banner */

.main-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-position: center;
  height: 650px;
  background-repeat: no-repeat;
  background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url(https://news.cgtn.com/news/2020-12-16/China-discovers-earliest-and-largest-rice-paddy-fields-in-the-world-WgnsFkxq12/img/06dd48b969084ab19f8e40fb83e5ab76/06dd48b969084ab19f8e40fb83e5ab76.png);
}

.main-banner h1 {
  margin: 0;
  font-size: 40px;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: 0em;
}

.main-banner p {
  font-family: Nunito;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0em;
  text-align: center;
}

.banner-card-wrapper {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  width: 380px;
  height: auto;
}

/* main-banner end*/
